.App{
    height: 100vh;
    padding: 0px 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.books{
    display: flex;
    gap: 10px;
}
.book{
    flex:1;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
}
img{
    width: 200px;
    height: 300px;
    object-fit: cover;
    background-color: aqua;
}
.delete{
    border: 1px solid;
    padding: 3px 6px;
    background-color: white;
    cursor: pointer;

}
.update{
    border: 1px solid;
    padding: 3px 6px;
    background-color: white;
    cursor: pointer;
}